import React, { useState } from 'react';
import './formNewsletter.css';
import { Trans, useTranslation } from 'react-i18next';
import MailchimpSubscribe from 'react-mailchimp-subscribe';
import { URL_MAILCHIMP } from 'assets/Constants';
import swal from 'sweetalert';
import $ from 'jquery';
import { RECAPTCHA_SITEKEY } from 'assets/Constants';
import ReCAPTCHA from "react-google-recaptcha";


const url = URL_MAILCHIMP;

function Form({ onSubmitted }) {

    const { t } = useTranslation();
    const [email, setEmail] = useState('');
    const handleSubmit = (event) => {

        event.preventDefault();

        onSubmitted({ EMAIL: email });
        swal({
            title: t('newsletter.message_success'),
            icon: "success",
            timer: 3000,
          });
        
          setTimeout(function() {
            event.target.reset(); // Resetea el formulario despus de 3 segundos (3000 milisegundos)
          }, 2000);
      };

    // ReCAPTCHA
    function onChange(value) {
        console.log("Captcha value:", value);
    }

    return (
        <form onSubmit={handleSubmit} className="newsletter text-center">
            <div className="form-group">
                <h2>
                    <label className='line-break' htmlFor="exampleInputEmail1">
                    <Trans i18nKey="newsletter.newsletter_title">
                        Subscribe to <span className='f--size-50 f_600 mb-1'><br></br>DreamBlog</span>
                    </Trans>
                    </label>
                </h2>
                <h3 className='f--size-18 f_p text-center p_color'>{t('newsletter.newsletter_description')}</h3>
                <small id="emailHelp" className="form-text text-muted">{t('newsletter.newsletter_info')}</small>
            </div>
            <div className='col-lg-5 col-sm-12 align-items-center'>
                <input 
                    type="email" 
                    className="f_600 w-100 cursor-pointer border-dark border px-3 b_color py-2 rounded-lg my-2 text-center" 
                    id="exampleInputEmail1" 
                    aria-describedby="emailHelp" 
                    placeholder="YOUR E-MAIL" 
                    onChange={(event) => setEmail(event.target.value)}
                    required />
                <button type="submit" className="f_600 w-100 cursor-pointer border-dark border px-3 b_color py-2 rounded-lg my-2 text-center">
                    {t('newsletter.newsletter_btn')}
                </button>
                <ReCAPTCHA
                    sitekey={`${RECAPTCHA_SITEKEY}`}
                    size="invisible"
                    onChange={onChange}
                    />
            </div>
        </form>
    );
  }

export default function CustomNewsletter ({typeNewsletter}) {

        /* Tiempo en que sale el Pop-up */
        $(document).ready(
            function() {
              $('.modal').delay(15000).queue(function() {
                $('.modal').modal('show')
              });
            }
          );

  return (
    <section className={`newsletter  ${typeNewsletter}`}>
        <div className='container modal-dialog modal-xl px-2'>
            <div className="modal-content">
                <MailchimpSubscribe 
                    url={url}
                    render={({ subscribe }) => (
                        <Form onSubmitted={formData => subscribe(formData)}/>
                    )}
                />
            </div>
        </div>
    </section>
  
  )
}


